<template>

    <!-- 分类男生 -->

    <div id="ClassifyBoy">
        
        <UniversalClassify
            :Theme="Theme"
            :HotList="HotList"
            :Finish="Finish"
            :DarkHorse="DarkHorse"
            :HotSearch="HotSearch"
            :items="items"
        >
        </UniversalClassify>

    </div>

</template>

<script>
import UniversalClassify from '@/components/UniversalClassify.vue';

export default {
    components: {
        UniversalClassify
    },
    data() {
        return {
            items: [
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318202433629.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318412350384.jpg"
                },
                {
                    img:"https://cdn.qimao.com/bookimg/zww/backendstatic/images/other/16103318321888182.jpg"
                },
            ],
            Theme: [],
            Unify: [
                "54d42d92321052167dfb75e3",
                "564eb878efe5b8e745508fde",
                "54d42e72d9de23382e6877fb",
                "5a6844f8fc84c2b8efaa8bc5",
            ],
            HotList: [],
            Finish: [],
            DarkHorse: [],
            HotSearch: [],
        }
    },
    activated() {
        this.$store.commit('classifyTwo');
        this.$store.commit('Eliminate');
    },
    created() {

        this.axios.get(`categories`).then((respaonse)=>{
            for(let i=0; i<12; i++) {
                var split = respaonse.data.male[i].bookCover[0].split("/");
                var obj = {
                    img: unescape(split[2]),
                    name: respaonse.data.male[i].name,
                }
                this.Theme.push(obj);
            }
        })

        this.traverse(this.Unify[0],this.HotList);
        this.traverse(this.Unify[1],this.Finish)
        this.traverse(this.Unify[2],this.DarkHorse)
        this.traverse(this.Unify[3],this.HotSearch)

    },
    methods: {
        traverse(n,arr) {
            this.axios.get(`rank/${n}`).then((respaonse)=>{
                for(let j=0; j<21; j++) {
                    var split = respaonse.data.ranking.books[j].cover.split("/");
                    var obj = {
                        id: respaonse.data.ranking.books[j]._id,
                        img: unescape(split[2]),
                        title: respaonse.data.ranking.books[j].title,
                        minorCate: respaonse.data.ranking.books[j].minorCate,
                        retention: respaonse.data.ranking.books[j].retentionRatio,
                        ranking: j+1
                    }
                    arr.push(obj);
                }
            })
        }
    },
}
</script>

