<template>

    <!-- 分类通用列表子组件 -->

    <li id="ClassifyGeneralListSon" @click="AccessDetails(item.id)" >

        <van-image
            :src="item.img"
        />

        <div class="text">

            <div class="top">
                <span class="name">{{item.title}}</span>
                <span class="score">
                    {{item.ranking}}
                </span>
            </div>

            <div class="bottom">
                <span class="writer">{{item.minorCate}}</span>
                <span class="popularity">{{item.retention}}万热度</span>
            </div>

        </div>

    </li>
</template>

<script>
export default {
    props: {
        item: {
            type: Object
        }
    },
    methods: {
        AccessDetails(id) {
            this.$store.commit('JudgeError')
            this.$router.push({
                path:"/noveldetails",
                query: {
                    id
                },
            })
        }
    },
}
</script>

<style lang="less">
    #ClassifyGeneralListSon {
        display: flex;
        margin-bottom: 20px;
        .van-image {
            width: 20%
        }
        .text {
            width: 74%;
            margin-left: 10px;
            display: flex;
            flex-wrap: wrap;
            .top {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items:center;
                .name {
                    width: 80%;
                    font-family: "黑体";
                    font-size: 18px;
                    color: #222222;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                .score {
                    line-height: 20px;
                    font-size: 18px;
                    font-weight: 600;
                    color: #fca000;
                    i {
                        font-size: 13px;
                        font-weight: 500;
                        font-style: normal;
                    }
                }
            }
            .bottom {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items:center;
                .writer {
                    font-size: 13px;
                    color: #999999;
                }
                .popularity {
                    border-radius: 2px;
                    font-size: 12px;
                    line-height: 16px;
                    padding: 2px 4px;
                    color: #ba7930;
                }
            }
        }
    }
</style>