<template>

    <!-- 分类通用列表组件 -->

    <div id="ClassifyGeneralList">

        <ul>
            <ClassifyGeneralListSon
                v-for="(item, index) in items"
                :key="index"
                :item="item"
            ></ClassifyGeneralListSon>
        </ul>

    </div>
</template>

<script>
import ClassifyGeneralListSon from '@/components/ClassifyGeneralListSon.vue'

export default {
    components: {
        ClassifyGeneralListSon
    },
    props: {
        HotList: {
            type:Array
        },
        Finish: {
            type:Array
        },
        DarkHorse: {
            type:Array
        },
        HotSearch: {
            type:Array
        }
    },
    data() {
        return {
            items: []
        }
    },
    mounted() {
        if(this.HotList) {
            this.items = this.HotList
        }
        if(this.Finish) {
            this.items = this.Finish
        }
        if(this.DarkHorse) {
            this.items = this.DarkHorse
        }
        if(this.HotSearch) {
            this.items = this.HotSearch
        }
    },
    watch: {
        HotList(n) {
            this.items = n
        },
        Finish(n) {
            this.items = n
        },
        DarkHorse(n) {
            this.items = n
        },
        HotSearch(n) {
            this.items = n
        }
    }
}
</script>

<style lang="less">
    #ClassifyGeneralList {
        padding: 10px;
        ul {
        }
    }
</style>